<template>
  <div>
    <van-nav-bar title="联系我们"/>
    <!-- <section>
    <div class="group-title">分享合作</div>
    <van-cell-group>
      <van-cell title="代理" is-link />
      <van-cell title="分享" is-link />
    </van-cell-group>
    </section>-->
    <section> 
    <div class="group-title">联系我们</div>
    <van-cell-group>
      <van-cell title="手机" value="12345678901" label="联系我们" url="tel:12345678901" is-link/>
      <van-cell title="短信" label="发送短信" url="sms:12345678901" is-link />
      <van-cell title="邮箱" value="865429505@qq.com" label="发送邮件" url="mailto:865429505@qq.com" is-link />
      <van-cell title="微信" label="扫一扫添加微信" @click="priviewCode" is-link>
        <img slot="right-icon" src="../assets/images/code.png" width="50" height="50" class="van-cell__right-icon" />
      </van-cell>
      <!-- <van-cell title="客服" is-link /> -->
    </van-cell-group>
    </section>
    <section>
    <div class="group-title">其他</div>
    <van-cell-group>
      <van-cell title="关于" is-link />
    </van-cell-group>
    </section>
  </div>
</template>
<script>
import {NavBar, CellGroup, Cell, ImagePreview} from 'vant'
export default {
  components: {
    [NavBar.name]: NavBar,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
  },
  methods: {
    priviewCode() {
      ImagePreview([require('../assets/images/code.png')])
    },
  },
}
</script>
<style scoped>
.group-title {
  font-weight: 400;
  font-size: 14px;
  color: rgba(69, 90, 100, 0.6);
  padding: 10px 15px 15px;
}
</style>



